.button {
  font-family: 'Inter';
  font-style: normal;
  font-weight: 700;
  font-size: 16px;
  line-height: 27px;
  background: #292929;
  border-radius: 40px;
  display: block;
  text-decoration: none;
  color: #fff;
  text-align: center;
  height: 56px;
  line-height: 56px;
  font-weight: 700;
  border-color: transparent;
  border-width: 2px;
  width: 324px;

  &__wrapper {
    display: flex;
    justify-content: center;
  }

  &:hover {
      background: #fff;
      color: #292929;
      border-color: #292929;
      border-width: 2px;
      border-style: solid;
    }

  &--form {
    margin-bottom: 50px;
    max-width: 280px;

    @include for-desktop() {
      max-width: 324px;
    }
  }
}